import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import { Subscription } from 'rxjs/Subscription';
import { MatDialog, MatDialogRef } from '@angular/material';

import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';

import { LeadsFormComponent } from './leads-form/leads-form.component';
import { LeadsService } from './leads.service';

@Component({
    selector     : 'fuse-leads',
    templateUrl  : './leads.component.html',
    styleUrls    : ['./leads.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations   : fuseAnimations
})
export class FuseLeadsComponent implements OnInit, OnDestroy
{
    hasSelectedLeads: boolean;
    selectedCount = 0;
    searchInput: FormControl;
    dialogRef: any;
    onSelectedLeadsChangedSubscription: Subscription;
    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;

    constructor(
        private leadsService: LeadsService,
        public dialog: MatDialog
    )
    {
        this.searchInput = new FormControl('');
    }

    ngOnInit()
    {
        this.onSelectedLeadsChangedSubscription =
            this.leadsService.onSelectedLeadsChanged
                .subscribe(selectedLeads => {
                    this.selectedCount = selectedLeads.length;
                });

        this.searchInput.valueChanges
            .debounceTime(300)
            .distinctUntilChanged()
            .subscribe(searchText => {
                this.leadsService.onSearchTextChanged.next(searchText);
            });
    }

    ngOnDestroy()
    {
        this.onSelectedLeadsChangedSubscription.unsubscribe();
    }

    newLead()
    {
        this.dialogRef = this.dialog.open(LeadsFormComponent, {
            panelClass: 'lead-form-dialog',
            data      : {
                action: 'new'
            }
        });

        this.dialogRef.afterClosed()
            .subscribe((response: FormGroup) => {
                if ( !response )
                {
                    return;
                }

                this.leadsService.updateLead({
                    ...response.getRawValue(),
                    created: new Date().toISOString().slice(0, 10)
                });

            });

    }

    deleteSelectedLeads()
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete all selected leads?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                this.leadsService.deleteSelectedLeads();
            }
            this.confirmDialogRef = null;
        });
    }
}



