import { Component, OnDestroy, OnInit, TemplateRef, ViewChild, ViewEncapsulation } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { MatDialog, MatDialogRef, MatPaginator, MatSort } from '@angular/material';
import { DataSource } from '@angular/cdk/collections';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { LeadsFormComponent } from '../leads-form/leads-form.component';

import { LeadsService } from '../leads.service';

import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import { Lead } from '../lead.model';

@Component({
    selector     : 'fuse-leads-list',
    templateUrl  : './leads-list.component.html',
    styleUrls    : ['./leads-list.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations   : fuseAnimations
})
export class FuseLeadsListComponent implements OnInit, OnDestroy
{
    @ViewChild('dialogContent') dialogContent: TemplateRef<any>;
    @ViewChild(MatPaginator) paginator: MatPaginator;
    @ViewChild(MatSort) sort: MatSort;

    leads: any;
    dataSource: LeadsDataSource | null;
    displayedColumns = ['checkbox', 'name', 'title', 'referredBy', 'status', 'accountName', 'phone', 'email', 'salesRep', 'dateCreated', 'createdBy', 'buttons'];
    selectedLeads: any[];
    checkboxes: {};

    onLeadsChangedSubscription: Subscription;
    onSelectedLeadsChangedSubscription: Subscription;

    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
    dialogRef: MatDialogRef<LeadsFormComponent>;

    constructor(
        private leadsService: LeadsService,
        private router: Router,
        public dialog: MatDialog
    )
    {
        this.onLeadsChangedSubscription =
            this.leadsService.onLeadsChanged.subscribe(leads => {
                this.leads = leads;

                this.checkboxes = {};
                leads.map(lead => {
                    this.checkboxes[lead.id] = false;
                });
            });

        this.onSelectedLeadsChangedSubscription =
            this.leadsService.onSelectedLeadsChanged.subscribe(selectedLeads => {
                for ( const id in this.checkboxes )
                {
                    if ( !this.checkboxes.hasOwnProperty(id) )
                    {
                        continue;
                    }

                    this.checkboxes[id] = selectedLeads.includes(id);
                }
                this.selectedLeads = selectedLeads;
            });
    }

    ngOnInit()
    {
        this.dataSource = new LeadsDataSource(
            this.leadsService,
            this.paginator,
            this.sort
        );
    }

    ngOnDestroy()
    {
        this.onLeadsChangedSubscription.unsubscribe();
        this.onSelectedLeadsChangedSubscription.unsubscribe();
    }

    editLead(lead)
    {
        this.dialogRef = this.dialog.open(LeadsFormComponent, {
            panelClass: 'lead-form-dialog',
            data      : {
                lead: lead,
                action : 'edit'
            }
        });

        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response )
                {
                    return;
                }
                const actionType: string = response[0];
                const formData: FormGroup = response[1];
                switch ( actionType )
                {
                    /**
                     * Save
                     */
                    case 'save':

                        this.leadsService.updateLead({
                            ...formData.getRawValue(),
                            created: lead.created
                        });

                        break;
                    /**
                     * Delete
                     */
                    case 'delete':

                        this.deleteLead(lead);

                        break;
                }
            });
    }

    /**
     * Delete Lead
     */
    deleteLead(lead)
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                this.leadsService.deleteLead(lead);
            }
            this.confirmDialogRef = null;
        });

    }

    onSelectedChange(leadId)
    {
        this.leadsService.toggleSelectedLead(leadId);
    }

    toggleAll(ev) {
        if (ev) {
            this.leadsService.toggleSelectAll();
        }
    }
}

export class LeadsDataSource extends DataSource<any>
{
    total = 0;

    private sub1: Subscription;

    constructor(
        private leadsService: LeadsService,
        private _paginator: MatPaginator,
        private _sort: MatSort
    ) {
        super();
    }

    /** Connect function called by the table to retrieve one stream containing the data to render. */
    connect(): Observable<Lead[]>
    {
        const displayDataChanges = [
            this.leadsService.onLeadsChanged,
            this._paginator.page,
            this._sort.sortChange
        ];


        this.sub1 = this.leadsService.onLeadsChanged
            .subscribe(leads => {
                setTimeout(() => {
                    this.total = leads.length;
                });
            });

        return Observable.merge(...displayDataChanges)
            .map(() => {
                let leads = this.leadsService.leads;

                leads = this.sortData(leads);

                const startIndex = this._paginator.pageIndex * this._paginator.pageSize;
                leads = leads.slice(startIndex, startIndex + this._paginator.pageSize);

                return leads;
            });
    }

    sortData(data): Lead[] {
        if ( !this._sort.active || this._sort.direction === '' )
        {
            return data;
        }

        return data.sort((a, b) => {
            let valueA, valueB;

            switch(this._sort.active) {
                case 'created':
                    valueA = new Date(a[this._sort.active]);
                    valueB = new Date(b[this._sort.active]);
                    break;
                case 'name':
                    valueA = a['firstname'] + ' ' + a['lastname'];
                    valueB = b['firstname'] + ' ' + b['lastname'];
                    break;
                default:
                    valueA = a[this._sort.active];
                    valueB = b[this._sort.active];
                    break;
            }

            return (valueA < valueB ? -1 : 1) * (this._sort.direction === 'asc' ? 1 : -1);
        });
    }

    disconnect()
    {
        this.sub1.unsubscribe();
    }
}



