import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Subject } from 'rxjs/Subject';

import { FuseUtils } from '@fuse/utils';

import { Lead } from './lead.model';

@Injectable()
export class LeadsService implements Resolve<any>
{
    onLeadsChanged: BehaviorSubject<any> = new BehaviorSubject([]);
    onSelectedLeadsChanged: BehaviorSubject<any> = new BehaviorSubject([]);
    onUserDataChanged: BehaviorSubject<any> = new BehaviorSubject([]);
    onSearchTextChanged: Subject<any> = new Subject();
    onFilterChanged: Subject<any> = new Subject();

    leads: Lead[];
    user: any;
    selectedLeads: string[] = [];

    searchText: string;
    filterBy: string;

    constructor(private http: HttpClient)
    {
    }

    /**
     * The Leads App Main Resolver
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns {Observable<any> | Promise<any> | any}
     */
    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
    {
        return new Promise((resolve, reject) => {
            this.getLeads().then(
                ([files]) => {

                    this.onSearchTextChanged.subscribe(searchText => {
                        this.searchText = searchText;
                        this.getLeads();
                    });

                    this.onFilterChanged.subscribe(filter => {
                        this.filterBy = filter;
                        this.getLeads();
                    });

                    resolve();

                },
                reject
            );
        });
    }

    getLeads(): Promise<any>
    {
        return new Promise((resolve, reject) => {
                this.http.get('api/leads')
                    .subscribe((response: any) => {

                        this.leads = response;

                        if ( this.searchText && this.searchText !== '' )
                        {
                            this.leads = FuseUtils.filterArrayByString(this.leads, this.searchText);
                        }

                        this.leads = this.leads.map(lead => {
                            return new Lead(lead);
                        });

                        this.onLeadsChanged.next(this.leads);
                        resolve(this.leads);
                    }, reject);
            }
        );
    }

    /**
     * Toggle selected lead by id
     * @param id
     */
    toggleSelectedLead(id)
    {
        // First, check if we already have that todo as selected...
        if ( this.selectedLeads.length > 0 )
        {
            const index = this.selectedLeads.indexOf(id);

            if ( index !== -1 )
            {
                this.selectedLeads.splice(index, 1);

                // Trigger the next event
                this.onSelectedLeadsChanged.next(this.selectedLeads);

                // Return
                return;
            }
        }

        // If we don't have it, push as selected
        this.selectedLeads.push(id);

        // Trigger the next event
        this.onSelectedLeadsChanged.next(this.selectedLeads);
    }

    /**
     * Toggle select all
     */
    toggleSelectAll()
    {
        if ( this.selectedLeads.length === this.leads.length )
        {
            this.deselectLeads();
        }
        else
        {
            this.selectLeads();
        }
    }

    selectLeads(filterParameter?, filterValue?)
    {
        this.selectedLeads = [];

        // If there is no filter, select all todos
        if ( filterParameter === undefined || filterValue === undefined )
        {
            this.selectedLeads = [];
            this.leads.map(lead => {
                this.selectedLeads.push(lead.id);
            });
        }
        else
        {
            /* this.selectedLeads.push(...
                 this.leads.filter(todo => {
                     return todo[filterParameter] === filterValue;
                 })
             );*/
        }

        // Trigger the next event
        this.onSelectedLeadsChanged.next(this.selectedLeads);
    }

    updateLead(lead)
    {
        return new Promise((resolve, reject) => {

            this.http.post('api/leads/' + lead.id, {...lead})
                .subscribe(response => {
                    this.getLeads();
                    resolve(response);
                });
        });
    }

    deselectLeads()
    {
        this.selectedLeads = [];

        // Trigger the next event
        this.onSelectedLeadsChanged.next(this.selectedLeads);
    }

    deleteLead(lead)
    {
        const leadIndex = this.leads.indexOf(lead);
        this.leads.splice(leadIndex, 1);
        this.onLeadsChanged.next(this.leads);
    }

    deleteSelectedLeads()
    {
        for ( const leadId of this.selectedLeads )
        {
            const lead = this.leads.find(_lead => {
                return _lead.id === leadId;
            });
            const leadIndex = this.leads.indexOf(lead);
            this.leads.splice(leadIndex, 1);
        }
        this.onLeadsChanged.next(this.leads);
        this.deselectLeads();
    }

}



