import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CdkTableModule } from '@angular/cdk/table';

import { MatButtonModule, MatChipsModule, MatDialogModule, MatFormFieldModule, MatIconModule, MatInputModule, MatPaginatorModule, MatRippleModule, MatSelectModule, MatSortModule, MatTableModule, MatTabsModule, MatCheckboxModule, MatMenuModule, MatSnackBarModule, MatToolbarModule, MatSidenavModule } from '@angular/material';

import { FuseSharedModule } from '@fuse/shared.module';
import { FuseWidgetModule } from '@fuse/components/widget/widget.module';


import { FuseLeadsComponent } from './leads.component';
import { LeadsService } from './leads.service';
import { LeadsFormComponent } from './leads-form/leads-form.component';
import { FuseLeadsListComponent } from './leads-list/leads-list.component';
import { FuseConfirmDialogModule } from '@fuse/components';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { ChartsModule } from 'ng2-charts';


const routes: Routes = [
    {
        path     : '',
        component: FuseLeadsComponent,
        resolve  : {
            data: LeadsService
        }
    },
];

@NgModule({
    declarations: [
        FuseLeadsComponent,
        LeadsFormComponent,
        FuseLeadsListComponent
    ],
    imports     : [
        RouterModule.forChild(routes),

        CdkTableModule,
        MatButtonModule,
        MatChipsModule,
        MatDialogModule,
        MatFormFieldModule,
        MatIconModule,
        MatInputModule,
        MatPaginatorModule,
        MatRippleModule,
        MatSelectModule,
        MatSortModule,
        MatTableModule,
        MatTabsModule,
        MatCheckboxModule,
        MatMenuModule,
        MatSnackBarModule,
        MatToolbarModule,
        FuseConfirmDialogModule, 
        MatSidenavModule,
        ChartsModule,
        NgxChartsModule,
        
        FuseSharedModule,
        FuseWidgetModule,
    ],
    providers   : [
        LeadsService,
    ],
    entryComponents: [LeadsFormComponent]
})
export class LeadsModule
{
}



